


/* Property */

@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap");

.App{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vw;

}
p {
  margin: 0;
}
.myRow {
  width: 100%;
  display: flex;
}
.center{
  display:flex;
  justify-content: center;
  align-items: center;
  align-content: center;
}
.flexStart{
  justify-content: flex-start;
  align-items: center;
  align-content: center;
  padding:0px;
}
.flexEnd{
  display:flex;
  justify-content: flex-end;
  align-items: center;
  align-content: center;
}
.input{
  width: 100%;
  padding: 14px;
  padding-top: 16px;
  padding-bottom: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  margin-bottom: 10px;
  display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
margin-top: 16px;
height: 27px;

mix-blend-mode: normal;
border-radius: 20px;

/* Inside auto layout */

flex: none;
order: 1;
flex-grow: 0;
}
html{
    background-color: #FDFDFD;
}
.link {
	cursor: pointer;
}
.flex-col-hcenter-vcenter {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.flex-row-vcenter-hstart {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.flex-hcenter {
  justify-content: center;
}

.flex-col-hstart-vstart {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.link{
    text-decoration: none;
    color: #000;

}

